/**************************************************
  Select/Input
 **************************************************/
select, input[type="text"], input[type="password"] {
 border-bottom: lightgrey 1px solid;
 border-top: rgba(0,0,0,0) 1px solid;
 -webkit-transition: ease-in-out border-bottom .2s;
 transition: ease-in-out border-bottom .2s;
}
input[type="text"]:invalid, input[type="password"]:invalid {
  border-bottom: #cc0000 1px solid;
}
select:focus, input[type="text"]:focus, input[type="password"]:focus {
 border-bottom: #003452 1px solid;
}
select option {
  background-color: rgb(245,245,245);
}
select.full-width, input[type="text"].full-width, input[type="password"].full-width, textarea.full-width {
  width: 100%;
}
textarea {
  overflow: hidden;
  resize: none;
  border: lightgrey 1px solid;
  -webkit-transition: ease-in-out border .2s;
  transition: ease-in-out border .2s;
}
textarea:focus {
  border: #003452 1px solid;
}
.row {
