input[type="range"] {
  -webkit-appearance: none;
  width: 100%;
  height: 5px;
  padding: 0px;
  border-radius: 2.5px;
  background: #e8e8e8;
  outline: none;
  opacity: 1;
  -webkit-transition: .2s;
  -webkit-transition: opacity .2s;
  transition: opacity .2s;
}
input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background: #003452;
  cursor: pointer;
}
input[type="range"]::-moz-range-thumb {
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background: #003452;
  cursor: pointer;
}
